.game-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;

    cursor: url("../img/hammer_from.svg") 50 50, pointer;
}

.game-container-hit {
    cursor: url("../img/hammer_to.svg") 15 15, pointer;
}

.game-board {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-column-gap: 2px;
    grid-row-gap: 2px;
}

.game-board-cell {
    position: relative;
    width: 64px;
    height: 64px;
    border: 1px solid black;
    background-color: aliceblue;
}

.game-goblin {
    background-image: url("../img/goblin.png");
    background-size: contain;
    width: 100%;
    height: 100%;
}

.message-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.3);
    z-index: 1;
}
.message {
    background-color: #ffffff;
}

.hide {
    display: none;
}